@import '../../variables.scss';
@import '../../mixin.scss';


h1,h2,h3,h4,h5,h6,
p,
dl,dt.dd {
    margin:0;
    padding:0;
}


.liquidate {
    position:relative;
    z-index: 1;
    line-height: 1;
    min-height: 68vh;
    .noData {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 20px;
      min-height: 44vh;
      text-align: center;
      line-height: 1.28;
    }
    .loadingWrap {
      margin: 20px 0;
    }
    .intro {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding-top: 80px;
      padding-bottom: 36px;
      background: $BgBlack;
      .introTitle {
        font-size: 36px;
        text-align: center;
        line-height: 43.2px;
        color: $PureWhite;
        font-weight: 700;
      }
      .introTips {
        font-size: 16px;
        line-height: 19.2px;
        text-align: center;
        font-weight: 400;
        color: $PureWhite;
        margin-top: 14px;
      }
    }
    .poolsContainer {
      background: linear-gradient(900deg, $BgBlack 40%, $Bg 0%);
      :global {
        .container {
          max-width: 1216px;
        }
      }
        .poolContainer {
            background: $PureWhite;
            border-radius: 16px;
            padding: 0 20px;
            margin-bottom: 108px;
            .infoContainer {
              display: flex;
              flex-direction: column;
              justify-content: flex-start;
              
              width: 100%;
              .header {
                  margin-left: 0;
                  margin-right: 0;
                  align-content: center;
                  min-height: 64px;
                  border-bottom: 1px solid $LineGrey;
                  font-size: 14px;
                  font-weight: 400;
                  color: $DarkenGrey;
                  line-height: 16.8px;

              }
              .titleTxt {
                position: relative;
                display: inline-block;
                padding-right: 16px;
                cursor: pointer;
                user-select: none;
                text-align: right;
                white-space: nowrap;
                &:before {
                  content: '';
                  position: absolute;
                  top: calc(50% - 9px);
                  right: 0;
                  border: 4px solid $LightenGrey3;
                  border-top: 4px solid transparent;
                  border-left: 4px solid transparent;
                  border-right: 4px solid transparent;
                  border-radius: 2px;
              }
              &:after {
                  content: '';
                  position: absolute;
                  bottom:calc(50% - 9px);
                  right: 0;
                  border: 4px solid $LightenGrey3;
                  border-bottom: 4px solid transparent;
                  border-left: 4px solid transparent;
                  border-right: 4px solid transparent;
                  border-radius: 2px;
              } 
              &.down:after {
                  border-top-color: $Green;
                  opacity: 1;
              }
              &.up::before {
                  border-bottom-color: $Green;
                  opacity: 1;
              }
            }
              .poolListWrap {
                min-height: 44vh;
              }
              .poolItemWrap {
                border-bottom: 1px solid $LineGrey;

              }
              .poolItem {
                  cursor: pointer;
                  margin-left: 0;
                  margin-right: 0;
                  &:hover {
                    background: $Bg;
                  }
                  .value {
                      display: flex;
                      flex-direction: column;
                      justify-content: center;
                      font-size: 14px;
                      line-height: 16.8px;
                      font-weight: 400;
                      padding: 10px 12px;
                      color: $DarkBlack;
                      >svg {
                        margin-top: 8px;
                      }
                      .valueHd {
                        display: none;
                      }
                      .valueContent {
                        .valueContentNum {
                          word-break: break-all;
                        }
                        .status {
                          font-size: 12px;
                          font-weight: 500;
                          line-height: 20px;
                          display: inline-block;
                          padding: 0 8px;
                          border-radius: 10px;
                          color: $Green;
                          background: rgba($Green, .16);
                          &.severe {
                            color: $Red2;
                            background: rgba($Red2, .16);
                          }
                          &.warning {
                            color: $Yellow2;
                            background: rgba($Yellow2, .16);
                          }
                        }
                      }
                  }   
                  .colOperate {
                    flex-direction: row;
                    justify-content: flex-start;
                    > * {
                      align-self: flex-start;
                    }
                    svg {
                      font-size: 16px;
                      margin-left: 10px;
                      margin-top: 8px;
                      color:$v2_brand-b;
                    }
                    >button {
                      text-transform: capitalize;
                    }
                  }
              }
              .poolAsset {
                padding: 32px 0; 
                margin-left: 0;
                margin-right: 0;
                background: $LightenGrey2;
                .colArrow {
                  align-self: center;
                }
                .actionStep {
                  font-weight: 400;
                  font-size: 14px;
                  line-height: 16.8px;
                  color: $DarkenGrey;
                }
              }
              .colExpand {
                width: 44px;
                font-size: 20px!important;
                svg {
                  color: $DarkenGrey;
                }
              }
              .colAccount {
                width: 130px;
                word-break: break-all;
                line-height: 1.2;
              }
              .colArrow {
                font-size: 16px!important;
                padding:0 10px!important;
                width:36px;
                svg {

                }
              }
              .colHealth {
                width: 160px;
                word-break: break-all;
                line-height: 1.2;
              }
              .colAssetValue {
                flex: 1;
              }
              .colOperate {
                width: 144px;
                justify-content: flex-end;
              }
              .colHealthAndOperate {
                width: 294px;
                text-align: right;
                .actionBtn {
                  display: none;
                }
              }
              .poolItemDisabledExpand {
                cursor:default;
                &:hover {
                  background: initial;

                }
              }
              .paginationContainer {
                  display: flex;
                  flex-direction: row;
                  justify-content: center;
                  align-items: center;
                  text-align: center;
                  margin-top: 32px;
                  margin-bottom: 20px;
                  .page-link:focus {
                      outline: none !important;
                      box-shadow: none !important;
                  }
              }
          }
        }
    }
    
    .assetList {
    }
    .assetArrow {
      transform: rotate(-90deg);
      font-size: 26px;
      svg {
        color: $DarkenGrey;
      }
    }
    .assetItem {
      max-width: 300px;
      display: flex;
      flex-direction: column;
      padding: 16px 20px;
      border: 1px solid transparent;
      margin-top: 20px;
      border-radius: 8px;
      background: $PureWhite;
      cursor: pointer;
      transition: all .4s ease;
      &:hover {
        transform: translateY(-4px);
        box-shadow: 0 8px 16px rgba($DarkBlack, .1);
      }
      img {
        width: 24px;
        margin-right: 8px;
      }
      .txtWrap {
        margin-top: 4px;
        dl {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          padding: 0;
          margin-top: 6px;
          flex-wrap: wrap;

          font-size: 14px;
          line-height: 16.8px;
          dt {
            margin:0;
            padding:0;
            letter-spacing: 0;
            margin:2px 0;
            font-weight: 400;
            color: $Grey;
          }
          dd {
            margin:0;
            padding:0;
            margin:2px 0;
            word-break: break-all;
            font-weight: 400;
            color: $DarkBlack;
          }
        }
      }
    }
    .assetItemSelected {
      position: relative;
      border: 1px solid $Green;
      &::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: 32px;
        height: 32px;
        background: url('../../images/freshBank/select.svg') no-repeat 1px -1px; 
      }
    }

}

.txnModal {
  :global {
    .contentContainer {
      .formContainer {
        min-height: 370px;
        .inputContainer {
          margin: 8px 0;
          width: 100%;
        }
      }
    }
    .buttonsContainer {
      button {
        text-transform: capitalize;
      }
    }
  }
  .formInputWrap {
    width: 100%;
    .formInfo {
      text-align: left;
      .formInfoHd {
        font-weight: 400;
        font-size: 14px;
        line-height: 16.8px;
        color: $DarkenGrey;
      }
    }
  }
  .formInputWrap + .formInputWrap {
    margin-top: 32px;
    margin-bottom: 20px;
  }

  .infoList {
    margin-bottom: 24px;
    width: 100%;
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    padding: 16px 12px;
    border: 1px solid $LightenGrey;
    border-radius:  8px;
    .infoItem {
      flex: 1;
      padding: 0 5px;
      .value {
        font-size: 14px;
        line-height: 16.8px;
        color: $Green;
        font-weight: 600;
        word-break: break-all;
      }
      .label {
        margin-top: 4px;
        font-size: 12px;
        line-height: 14.4px;
        color: $DarkenGrey;
      }
    }
  }
}


@media (min-width: 1px) and (max-width: 991.98px) {
  .liquidate {
    .poolsContainer {
      .poolContainer {
        padding: 0 10px;
        .infoContainer {
          .header {
            display: none;
          }
          .poolItem {
            .value {
              .valueHd {
                display: inline-block;
              }
              &.colExpand {
                order:1;
                width: 100%;
                align-items: flex-end;
              }
              &.colAccount,
              &.colHealth,
              &.colBorrowAsset,
              &.colSupplyAsset {
                display: initial;
                width: 50%;
                .valueHd {
                  text-align: left;
                }
                .valueContent {
                  margin-top: 4px;
                }
                
              }
              &.colAccount,
              &.colHealth {
                order: 2;
              }
              &.colBorrowAsset,
              &.colSupplyAsset {
                order: 3;
              }
              &.colOperate {
                display: none;
              }
            }
          }
          .poolAsset {
            padding: 20px 5px;
            .colExpand,
            .colAccount,
            .colArrow {
              display: none;
            }
            .assetItem {
              padding: 10px;
              .txtWrap {
                dl {
                  flex-direction: column;
                }
              }
            }
            .colBorrowAsset,
            .colSupplyAsset {
              padding: 0 5px;
              order:1;
              width: 50%;
              .assetItem {
                width: 100%;
                max-width: initial;
              }
            }
            .colHealthAndOperate {
              order:2;
              width: 100%;
              text-align: center;
              margin-top: 20px;
              .actionBtn {
                display: block;
                margin-top: 20px;
              }
            }
          }
        }
      }
    }
  }
}

